.blocksMain {
    width: 100vw;
    height: fit-content;
    position: relative;
   height: fit-content;
}

.blocksMain::before {
    background-image: url("../../public/static/pngwing.png");
    background-repeat: no-repeat;
    background-size:cover;
    backdrop-filter: blur(16px);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.1;
    content: "";
    
}

.blocksPage h3 {
    width: fit-content;
    font-size: 36px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400px;
    margin: auto;
    padding: 25px 0;
}

.blocks {
    margin: 25px auto;
    width: 75%;
    height: 95%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.blocks a {
    color:white;
    width: fit-content;
    margin: 25px;
}

.block {
    width: 480px;
    height: 480px;
    position: relative;
    background-repeat: no-repeat;
    background-size:cover;
    overflow: hidden;
    outline: none;
    margin: 10px 100px;
    background-color: transparent;
    margin: 1.5%;
    border-radius: 30px;
    
    
}

.block h2 {
    font-size: 32px;
    position:absolute;
    z-index: 0;
    font-family: 'Montseratt', sans-serif;
    font-weight: 500;
    bottom: 0%;
    left: 0%;
    text-align: left;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(6px);
    line-height: 32px;
    padding: 1rem;
}

