.productsPage {
    padding-top: 80px;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    /* background-image: url("../static/photo_2024-02-14_12-05-07.jpg");
    background-size: contain;
    background-repeat: no-repeat; */
}

.productsPage * {
    font-weight: 500;
}

.product {
    width: 75%;
    margin: 25px auto;
    display: flex;
}

.product h1 {
    font-size: 48px;
}

.product .h1main {
    font-size: 64px;
    margin: 15px 0;
    background-color: rgb(71, 35, 214);
    border: 1px solid transparent;
    border-radius: 10px;
    width: fit-content;
    padding-left: 5px;
    padding-right: 5px;
}

.product h2 {
    margin: 100px 0;
    font-size: 36px;
    text-align: justify;
}

.productsPage h3 {
    font-size: 48px;
    margin-bottom: 25px;
}

.productsPage h4 {
    font-size: 36px;
}

.productsPage p {
    font-size: 24px;
    line-height: 30px;
    text-align: justify;
}

.productDesc {
    width: 50%;
}

.productsPage td {
    border: 1px solid ;
    padding: 10px;
    font-size: 20px;
}

.productsPage table {
    border: 1px solid ;
    border-collapse: collapse;
    margin: 15px 0;
}

@media screen and (max-width: 1024px) {
    .productsPage .h1main {
        font-size: 28px;
    }
    
    .productsPage .productDesc {
        width: 100%;
    }

    .productsPage h1 {
        font-size: 24px;
    }

    .productsPage h2 {
        font-size: 22px;
        text-align: left;
    }

    .productsPage h3 {
        font-size: 20px;
    }

    .productsPage p {
        font-size: 18px;
        text-align: justify;
    }

    .productsPage h4 {
        font-size: 18px;
    }

    .productsPage td {
        font-size: 16px;
        padding: 5px;
    }
}
